a,
.control {
  color: #ef9d6a;
}
.bfloat > svg {
  fill: #ef9d6a;
}
.bmodal hr {
  border-top: 1px solid #ef9d6a;
}
#banner {
  background: rgba(0, 0, 0, 0.7);
  border-bottom: 1px solid #282a2e;
}
b {
  color: #c5c8c6;
}
b.admin {
  color: #f00000;
}
b.moderator {
  color: #ef9d6a;
}
blockquote a:hover {
  color: #ef9d6a;
}
a:hover,
blockquote a,
article.editing a:hover {
  color: url(wood.jpg);
}
article,
aside,
.modal,
.bmodal,
.pagination {
  background: url(wood.jpg);
  color: white;
}
article.highlight,
.preview,
.popup-menu {
  background: url(wood.jpg);
  color: white;
}
article.editing {
  background: linear-gradient(rgba(239, 157, 106, 0.25), rgba(239, 157, 106, 0.25)), url(wood.jpg);
  color: white;
}
article.editing a,
article.editing header {
  color: white;
}
article.editing em {
  color: #B7BA3E;
}
article.editing li {
  color: white;
}
article.editing .themed {
  color: white;
}
#banner {
  color: white;
}
body {
  background: url(tavern.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
body,
pre,
.themed,
a > b {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  color: white;
}
.embed {
  background-color: white;
  text-shadow: none;
}
em {
  color: #B7BA3E;
}
h1 {
  font: bolder 28px Tahoma;
  letter-spacing: -2px;
  color: white;
}
h2 {
  font-family: Tahoma;
  color: white;
}
h3 {
  color: white;
}
hr {
  border-top: 1px solid #282a2e;
}
.popup-menu li:hover {
  background-color: #136;
  color: white;
}
.omit {
  color: white;
}
#lock {
  background-image: url('ui/invlock.svg');
  background-size: 1em 1em;
}
.option_tab_sel a.tab_sel {
  color: #FFD897;
}
.themed {
  color: white;
}
#banner {
  background: url(wood.jpg);
}
